#slider{
    position: relative;
    overflow: hidden;
    height: 630px;
    width:100vw;
}

.slide{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
      
}




.bgimage{
    height: 630px;
    width:100vw;
    
    position:relative;
    overflow: hidden;
}

.content{
    position:absolute;
    bottom : 70px;
    right:80px;
    width:600px;
    opacity:1;
    background-color: rgba(255,255,255,0.8);
    padding: 35px;
    color: #4C4C4E;
    
    
    
}

.content h1{
    margin-bottom: 15px;
}

.content p{
    line-height: 20px;
}


.titlecontent{
    position:absolute;
    top : 50px;
    left:100px;
    width:600px;
    opacity:1;
    color: white;
    padding: 35px;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}



@media screen and (max-width: 768px) {

    .content {       
        left:0;
        right:0;
        width: calc(var(--width)/2);      
    }

    .titlecontent{
        left: 40px;
    }
  
}




.normal{
    transition: display ease-in-out 0.4s;
    display: none;

}

.current{
    display:visible;
}


.button{
    border: 1px solid black;
    background-color: white;
    cursor: pointer;
    padding: 13px 15px;
    border-radius: 50%;
    outline:none;
}

.button :hover{
    cursor:pointer;
}



#prevbutton{
    position: absolute;
    top:50%;
    left: 15px;
}

#nextbutton{
    position: absolute;
    top:50%;
    right: 25px;
}

.container-dots {
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
  }

.dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 1px solid black;
    margin: 0 5px;
    background: white;
}


.dot.active {
    background: rgb(32, 32, 32);
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    cursor:pointer;
}


@media screen and (max-width: 768px) {
    .button{
        
        padding: 8px 8px;
        
    }
    
    .dot {
        width: 5px;
        height: 5px;
    }
  
  
}


.card{
    padding: var(--spacing-small);
    -moz-osx-font-smoothing:grayscale;
    -webkit-backface-visibility:hidden;
            backface-visibility:hidden;
    -webkit-transform:translateZ(0);
            transform:translateZ(0);
    transition:-webkit-transform 0.25s ease-out;
    transition:transform 0.25s ease-out;
    transition:transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
    border-width:.25rem;
    box-shadow:4px 4px 8px 0px rgba( 0, 0, 0, 0.2 );
    border-radius: 10px;
    width: 220px;
    cursor: pointer;
}
.divBorder{
    float:left;
    margin:10px;
    height:350px;
    padding: 18px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
}

.divBorder:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}
.titleBorder{
    text-align: center;
    width: 200px;
    white-space: pre-line;
    
}

.invalid{
    margin: 80px;
}


